import React, { memo, forwardRef, useCallback, useMemo, useState } from "react";
import styled, { css } from "styled-components";
import { IconChecked } from "../icons";
import { colors } from "../styles/colors";
export interface CheckboxColorSetsProps {
  //default
  /**
   * @default transparent
   * @description replace default background color
   */
  defaultBG?: string;
  /**
   * @default noColor.Grey11 #545454
   * @description replace default border color
   */
  defaultBorder?: string;
  /**
   * @default transparent
   * @description replace default background color when hover
   */
  defaultBGHover?: string;
  /**
   * @default brandColor.Primary8 #3B6A80
   * @description replace default border color when hover
   */
  defaultBorderHover?: string;

  // active
  /**
   * @description replace checked icon color
   * @default #fff
   */
  activeIcon?: string;
  /**
   * @default brandColor.Primary5 #5FA2C0
   * @description replace background color when active
   */
  activeBG?: string;
  /**
   * @default brandColor.Primary5 #5FA2C0
   * @description replace border color when active
   */
  activeBorder?: string;
  /**
   * @default brandColor.Primary8 #3B6A80
   * @description replace background color when active & hover
   */
  activeBGHover?: string;
  /**
   * @default brandColor.Primary8 #3B6A80
   * @description replace border color when active & hover
   */
  activeBorderHover?: string;

  // disabled
  /**
   * @default noColor.Grey2 #F2F2F2
   * @description replace background color when disabled
   */
  disabledBG?: string;
  /**
   * @default noColor.Grey6 #ABABAB
   * @description replace border color when disabled
   */
  disabledBorder?: string;

  // focused
  /**
   * @default transparent
   * @description replace focused outline color
   */
  focusedOutline?: string;
}

export const defaultCheckboxColorSets: CheckboxColorSetsProps = {
  defaultBG: "transparent",
  defaultBorder: colors.noColor.Grey11,
  defaultBGHover: "transparent",
  defaultBorderHover: colors.brandColor.Primary8,
  activeBG: colors.brandColor.Primary5,
  activeBorder: colors.brandColor.Primary5,
  activeBGHover: colors.brandColor.Primary8,
  activeBorderHover: colors.brandColor.Primary8,
  disabledBG: colors.noColor.Grey2,
  disabledBorder: colors.noColor.Grey6,
  focusedOutline: "transparent",
};

export interface CheckboxProps
  extends React.InputHTMLAttributes<HTMLInputElement> {
  indeterminate?: boolean;
  /**
   * @default 24
   * @description width & height
   */
  size?: number;
  /** @description replace default checked icon */
  checkIcon?: React.ReactElement;
  indeterminateIcon?: React.ReactElement;
  innerBoxStyles?: React.CSSProperties;
  /**
   * @description replace default color sets
   * @example {
      ...defaultCheckboxColorSets,
      activeIcon: "#b9b5ff",
      defaultBG: "#cbfffc",
    }
   */
  colorSets?: CheckboxColorSetsProps;
}

const Checkbox = forwardRef<HTMLInputElement, CheckboxProps>(
  (
    {
      checkIcon,
      indeterminate,
      indeterminateIcon,
      size,
      onChange,
      innerBoxStyles,
      colorSets = defaultCheckboxColorSets,
      ...rest
    },
    ref
  ) => {
    // inner status
    const [_checked, setChecked] = useState(rest.defaultChecked);
    const checked = typeof rest.checked === "boolean" ? rest.checked : _checked;
    const style = useMemo(() => {
      const s = rest.style ? rest.style : {};
      if (size) return { ...s, width: size, height: size };
      return s;
    }, [size, rest.style]);
    const _onChange = useCallback(
      (e: React.ChangeEvent<HTMLInputElement>) => {
        if (rest.readOnly || rest.disabled || indeterminate) return;
        if (onChange) onChange(e);
        // rest.checked has value, use parent's callback to update status
        if (typeof rest.checked !== "boolean") setChecked(e.target.checked);
      },
      [onChange, rest.checked, rest.disabled, rest.readOnly, indeterminate]
    );
    return (
      <Container
        style={style}
        checked={checked}
        disabled={rest.disabled}
        readOnly={rest.readOnly}
        indeterminate={indeterminate}
        colorSets={colorSets}
        size={size}
      >
        <input {...rest} type="checkbox" ref={ref} onChange={_onChange} />
        <InnerBox style={innerBoxStyles}>
          {!indeterminate && checked && (checkIcon || <CheckedIcon />)}
          {indeterminate && !!indeterminateIcon && indeterminateIcon}
        </InnerBox>
      </Container>
    );
  }
);
export default memo(Checkbox);

const defaultWidth = 24;

export const InnerBox = styled.div`
  ${(p) => p.theme.flexCSS.row}
  ${(p) => p.theme.flexCSS.center}  
  ${(p) => p.theme.absoluteFillCSS}
  pointer-events: none;
  width: 100%;
  height: 100%;
  transition: all 0.2s ease;
`;

const IconStyle = css`
  width: 70%;
  height: 70%;
  pointer-events: none;
`;

const CheckedIcon = styled(IconChecked)`
  ${IconStyle}
`;

export const Container = styled.div<CheckboxProps>`
  position: relative;
  flex: none;
  ${(p) => p.theme.flexCSS.row}
  ${(p) => p.theme.flexCSS.center}
  width: ${defaultWidth}px;
  height: ${defaultWidth}px;
  color: ${(p) =>
    p.checked ? p.colorSets?.activeIcon || "#fff" : "transparent"};

  input {
    width: 100%;
    height: 100%;
    opacity: 0;
    margin: 0;
    padding: 0;
    cursor: pointer;

    &[readonly="readonly"],
    &[readonly] {
      cursor: auto;
    }

    &:disabled {
      cursor: not-allowed;
    }
  }

  ${({ disabled, checked, colorSets }) => {
    if (disabled)
      return css`
        ${InnerBox} {
          border-color: ${colorSets?.disabledBorder ||
          defaultCheckboxColorSets.disabledBorder};
          background-color: ${colorSets?.disabledBG ||
          defaultCheckboxColorSets.disabledBG};
        }
      `;
    if (checked)
      return css`
        ${InnerBox} {
          border-color: ${colorSets?.activeBorder ||
          defaultCheckboxColorSets.activeBorder};
          background-color: ${colorSets?.activeBG ||
          defaultCheckboxColorSets.activeBG};
        }

        &:hover {
          ${InnerBox} {
            border-color: ${colorSets?.activeBorderHover ||
            defaultCheckboxColorSets.activeBorderHover};
            background-color: ${colorSets?.activeBGHover ||
            defaultCheckboxColorSets.activeBGHover};
          }
        }
      `;
    return css`
      ${InnerBox} {
        border-color: ${colorSets?.defaultBorder ||
        defaultCheckboxColorSets.defaultBorder};
        background-color: ${colorSets?.defaultBG ||
        defaultCheckboxColorSets.defaultBG};
      }

      &:hover {
        ${InnerBox} {
          border-color: ${colorSets?.defaultBorderHover ||
          defaultCheckboxColorSets.defaultBorderHover};
          background-color: ${colorSets?.defaultBGHover ||
          defaultCheckboxColorSets.defaultBGHover};
        }
      }
    `;
  }};

  ${({ size, colorSets }) => {
    const _w = (size || defaultWidth) / defaultWidth;
    return css`
      ${InnerBox} {
        outline: ${_w}px solid transparent;
        outline-offset: ${_w}px;
        border-style: solid;
        border-width: ${_w}px;
        border-radius: ${_w * 2}px;
      }

      input:focus ~ ${InnerBox} {
        outline-color: ${colorSets?.focusedOutline ||
        defaultCheckboxColorSets.focusedOutline};
      }
    `;
  }}
`;
